export default {
	data() {
		return {
			
			selectedTab:0,
			cultuerTeacherLedgerList:null,
			schoolId:null,
			pageNum:1,
			teacherInfo:null,
			loadModal:false
		}
	},
	methods: {
		tabSelect(e) {
			
			var id = e.currentTarget.dataset.id
			this.selectedTab = e.currentTarget.dataset.id
			
		},
		getTeacherInfo()
		{
				var me = this
				uni.request({
				url: this.serverUrl + '/led/school/teacher/' +this.schoolId + '/' + this.pageNum + '?limit=1' ,
				method: 'GET',
				data: {},
				success: res => {
						if(this.teacherInfo!=null && this.teacherInfo.id == res.data[0].id)
					{
						uni.showToast({
							title: "到底了!"
						})
						this.pageNum--
						this.loadModal = false
						return
					}
					me.teacherInfo = res.data[0]
					this.loadModal = false
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		prevent()
		{
			if(this.pageNum>1)
			{
				this.loadModal = true
				this.pageNum--;
				this.getTeacherInfo()
			}
		},
		next()
		{
			this.pageNum++
			this.loadModal = true
			this.getTeacherInfo()
		}
	},
	onLoad(param) {
		this.schoolId = param.schoolId
		uni.request({
			url: this.serverUrl + '/led/school/ledger/json/' + param.schoolId ,
			method: 'GET',
			data: {},
			success: res => {
				this.cultuerTeacherLedgerList = res.data.reverse()
				console.log(res.data)
			},
			fail: () => {},
			complete: () => {}
		});
		
		this.getTeacherInfo();
		
	
	}
}
